import * as React from 'react';
import { DisplayMode } from '@microsoft/sp-client-base';

import CanvasZone, { ICanvasZoneProps } from './CanvasZone';

export interface ICanvasRowProps {
  canvasZoneProps: ICanvasZoneProps;
  displayMode: DisplayMode;
  key: number;
  rowIndex: number;
}

export default class CanvasRow extends React.Component<ICanvasRowProps, void> {
  constructor(props: ICanvasRowProps) {
    super(props);
  }

  public render(): React.ReactElement<ICanvasRowProps> {
    return (
      <div>
        <div className='ms-Grid-row'>
          <CanvasZone {...this.props.canvasZoneProps}/>
        </div>
      </div>
    );
  }
}
